.gpt3__possibility {
    display: flex;
    flex-direction: row;
}

.gpt3__possibility-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: end;

    margin-right: 2rem;
}

.gpt3__possibility-image img {
    width: 100%;
    height: auto;
    max-width: 700px;
}

.gpt3__possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-self: flex-end;
}

.gpt3__possibility-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;

    color: var(--color-text);
}

.gpt3__possibility-content h4:last-child {
    color: var(--color-subtext);
}

.gpt3__possibility-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;

    margin: 1rem 0;
}

.gpt3__possibility-content p {
    font-family: var(--font-family);
    font-weight: normal;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);

    margin-bottom: 2rem;
}

@media screen and (max-width: 1000px) {
    .gpt3__possibility {
        flex-direction: column;
    }

    .gpt3__possibility-image img {
        max-width: 500px;
        margin-bottom: 4rem;
    }
}
