.gpt3__features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.gpt3__features-heading {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;

    text-align: left;
    margin-right: 5rem;
}

.gpt3__features-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}

.gpt3__features-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;

    color: var(--color-subtext);

    margin-top: 2rem;
}

.gpt3__features-container {
    flex: 1.5;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-right: 0;
}

@media screen and (max-width: 1200px) {
    .gpt3__features {
        flex-direction: column;
    }

    .gpt3__features-container {
        margin-top: 2rem;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__features {
        flex-direction: column;
    }

    .gpt3__features-container {
        margin-top: 2rem;
    }
}